#pluma {
    position: relative;
    left: 0;
    top: 250%;
    z-index: -7;
    -webkit-animation: pluma-animation 16s infinite;
            animation: pluma-animation 16s infinite;
    opacity: 0.6;
   
}

@-webkit-keyframes pluma-animation {
    0% {
        left: 0%;
    }

    60% {
        left: 40%;
        top: 5%;
    }

    100% {
        left: 0%;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes pluma-animation {
    0% {
        left: 0%;
    }

    60% {
        left: 40%;
        top: 5%;
    }

    100% {
        left: 0%;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

 #rama {
    position: relative;
    left: 0;
    bottom: -132%;
    z-index: -7;
    -webkit-animation: rama-animation 12s infinite;
            animation: rama-animation 12s infinite;
   
}

@-webkit-keyframes rama-animation {
    0% {
        left: 0%;
        bottom: -132%;
        -webkit-transform: rotate(240deg);
                transform: rotate(240deg);
        opacity: 1;
    }

    60% {
        left: -50%;
        bottom: 5%;
    }

    100% {
        left: -90%;
        bottom: 9%;
        opacity: 0;
    }
}

@keyframes rama-animation {
    0% {
        left: 0%;
        bottom: -132%;
        -webkit-transform: rotate(240deg);
                transform: rotate(240deg);
        opacity: 1;
    }

    60% {
        left: -50%;
        bottom: 5%;
    }

    100% {
        left: -90%;
        bottom: 9%;
        opacity: 0;
    }
}

#oval {
    position: relative;
    left: 7%;
    top: -10%;
    z-index: -7;
    -webkit-animation: oval-animation 12s infinite;
            animation: oval-animation 12s infinite;
   
}

@-webkit-keyframes oval-animation {
    0% {
        left: 7%;
        top: -50%;
        opacity: 1;
    }

    100% {
        left: 30%;
        top: 300%;
        opacity: 0;
    }
}

@keyframes oval-animation {
    0% {
        left: 7%;
        top: -50%;
        opacity: 1;
    }

    100% {
        left: 30%;
        top: 300%;
        opacity: 0;
    }
}

#cuadrado {
    position: relative;
    left: -30%;
    bottom: 28%;
    z-index: -7;
    -webkit-animation: cuadrado-animation 12s infinite;
            animation: cuadrado-animation 12s infinite;
    
}

@-webkit-keyframes cuadrado-animation {
    0% {
        left: -30%;
        bottom: 48%;
        opacity: 1;
    }

    100% {
        left: 80%;
        opacity: 0;
        bottom: 8%;
    }
}

